import React, { Component, Fragment } from 'react'

export default class Prices extends Component {
  state = {
    currency: 'USD'
  }
  render() {
    const key = [this.props.bpi]
    const country = key.map((area) => (
      <li className="list-group-item">
        Bitcoin rate for {area.description}: <span className="badge badge-primary">{area.code}</span> <strong>{area.rate}</strong>
      </li>
    ))
    return (
      <Fragment>
        <ul className="list-group">

        </ul>
      </Fragment>
    )
  }
}
